import mongoose from 'mongoose';
import connectDB from '../../../../lib/db';
import { getTokenFromHeader, verifyToken } from '../../../../lib/jwt';

// Define the Property model
const propertySchema = new mongoose.Schema({
  title: String,
  images: [String],
  location: String,
  price: Number,
  owner: {
    type: mongoose.Schema.Types.ObjectId,
    required: true
  }
});

const Property = mongoose.models.Property || mongoose.model('Property', propertySchema);

// Define the Enquiry model
const enquirySchema = new mongoose.Schema({
  name: {
    type: String,
    required: true
  },
  email: {
    type: String,
    required: true
  },
  phone: {
    type: String,
    required: true
  },
  message: {
    type: String,
    required: true
  },
  property: {
    type: mongoose.Schema.Types.ObjectId,
    ref: 'Property',
    required: true
  },
  status: {
    type: String,
    enum: ['pending', 'contacted', 'resolved', 'archived'],
    default: 'pending'
  },
  createdAt: {
    type: Date,
    default: Date.now
  }
});

const Enquiry = mongoose.models.Enquiry || mongoose.model('Enquiry', enquirySchema);

export async function GET(request) {
  try {
    const authHeader = request.headers.get('authorization');
    if (!authHeader || !authHeader.startsWith('Bearer ')) {
      return new Response(JSON.stringify({ error: 'Unauthorized' }), {
        status: 401,
        headers: { 'Content-Type': 'application/json' },
      });
    }

    const token = authHeader.split(' ')[1];
    const decoded = verifyToken(token);
    
    if (!decoded || !decoded.userId) {
      return new Response(JSON.stringify({ error: 'Invalid token' }), {
        status: 401,
        headers: { 'Content-Type': 'application/json' },
      });
    }

    await connectDB();
    
    // Get enquiries for properties owned by the user
    const userProperties = await Property.find({ owner: decoded.userId });
    const propertyIds = userProperties.map(prop => prop._id);

    const [receivedEnquiries, sentEnquiries] = await Promise.all([
      // Enquiries received for user's properties
      Enquiry.find({ property: { $in: propertyIds } })
        .populate('sender', 'name email')
        .populate('property', 'title images')
        .sort({ createdAt: -1 }),
      
      // Enquiries sent by the user
      Enquiry.find({ sender: decoded.userId })
        .populate('recipient', 'name email')
        .populate('property', 'title images')
        .sort({ createdAt: -1 })
    ]);

    return new Response(JSON.stringify({ received: receivedEnquiries, sent: sentEnquiries }), {
      status: 200,
      headers: { 'Content-Type': 'application/json' },
    });
  } catch (error) {
    console.error('Error fetching enquiries:', error);
    return new Response(JSON.stringify({ error: 'Failed to fetch enquiries' }), {
      status: 500,
      headers: { 'Content-Type': 'application/json' },
    });
  }
}
